.rewiev {
  padding: 80px 20px 86px 20px;

  &__img {
    margin: 0 auto;
    margin-bottom: 30px;
  }

  &__comment {
    padding: 0 50px 26px 50px;
    background-color: #fafafa;
    margin-bottom: 30px;
  }

  &__text {
    display: block;
    padding-top: 43px;
    padding-bottom: 10px;
    position: relative;
    z-index: 3;

    font-weight: 500;
    font-size: 12px;
    line-height: 2.55;
    text-align: center;
    color: #000000;

    background-image: repeating-linear-gradient(
        to bottom,
        transparent,
        transparent 30px,
        #e1e1e1 30px,
        #e1e1e1 31px
      ),
      url(/images/Customers/breckets.png);
    background-repeat: no-repeat;
    background-position-y: 38px, 29px;
  }

  &__poits {
    position: relative;
    display: block;
    height: 4px;
    width: 4px;
    background-color: #c7133e;
    margin: 0 auto;
    margin-bottom: 18px;
    border-radius: 50%;

    &::after,
    &::before {
      position: absolute;
      display: block;
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      background-color: #c7133e;
      border-radius: 50%;
    }

    &::after {
      right: -14px;
    }

    &::before {
      left: -14px;
    }
  }

  &__location {
    text-align: center;
    margin-bottom: 30px;
  }
}
